<template>
  <div class="real-status">
    <div class="device-status-info-layout">
      <DeviceStatusInfo3
        :icon="baseInfo.icon"
        :total="readStatus.total"
        :online="readStatus.online"
        :leave="readStatus.leave"
        :offline="readStatus.offline"
        :label="baseInfo.label"
        :showLeave="false"
      />
    </div>
    <div class="content-layout">
      <div class="data-box">
        <div class="search-box">
          <a-input-search
            class="operation-screen-input-search"
            v-model:value="searchValue"
            placeholder="请输入电站名称"
            style="width: 100%"
            @search="handleSearch"
          />
        </div>
        <div class="list-box">
          <div
            v-for="(item, itemIndex) in readList"
            :key="itemIndex"
            @click="handleClick(item, itemIndex, tabIndex)"
            :class="['list-item', selectedIndex === itemIndex ? 'selected' : null]"
          >
            <div
              class="list-item-label"
              :title="`${item.stationShortName}-${item[baseInfo.field]}`"
            >
              {{ item.stationShortName }}-{{ item[baseInfo.field] }}
            </div>
            <div :class="`list-item-status ${item.status}`">{{ item.statusText }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts" setup>
  import { ref, onMounted, watch } from 'vue';
  import DeviceStatusInfo3 from '/@/components/Framework/OperationScreen/DeviceStatusInfo3.vue';
  import * as SmartDeviceApi from '/@/api/monitor/smartdevice';
  import { MsgManager } from '@/message/MsgManager';

  const props = defineProps({
    tabIndex: {
      type: String,
      require: true,
      default: () => '',
    },
  });
  const deviceType = {
    '2': { code: 'ewc', field: 'userName', icon: 'dianzigongpai', label: '电子工牌' },
    '3': { code: 'ii', field: 'deviceName', icon: 'xunjianzhongduan', label: '巡检仪' },
    '4': { code: 'dr', field: 'plateNumber', icon: 'hangchejiluyi', label: '行车记录仪' },
  };
  const baseInfo = ref({
    icon: deviceType[props.tabIndex].icon,
    label: deviceType[props.tabIndex].label,
    field: deviceType[props.tabIndex].field,
  });
  const readStatus = ref({ total: 0, online: 0, leave: 0, offline: 0 });
  const readStatusShow = ref({ leave: false });
  const readList = ref([]) as any;
  const selectedIndex = ref<number>(-1);
  const searchValue = ref('');

  const getParams = () => {
    return { stationName: searchValue.value };
  };

  const initReadStatus = async (index) => {
    const params = getParams();
    readStatus.value = { total: 0, online: 0, leave: 0, offline: 0 };
    readStatusShow.value.leave = false;
    readList.value = [];
    selectedIndex.value = -1;
    if (index === '2') {
      // 电子工牌
      SmartDeviceApi.getEwcRealStatusCount(params).then((data) => {
        readStatus.value = data;
        readStatusShow.value.leave = false;
      });
      readList.value = await SmartDeviceApi.getEwcList(params);
    } else if (index === '3') {
      // 巡检仪
      SmartDeviceApi.getIiRealStatusCount(params).then((data) => {
        readStatus.value = data;
        readStatusShow.value.leave = true;
      });
      readList.value = await SmartDeviceApi.getIiList(params);
    } else if (index === '4') {
      // 行车记录仪
      SmartDeviceApi.getDrRealStatusCount(params).then((data) => {
        readStatus.value = data;
        readStatusShow.value.leave = true;
      });
      readList.value = await SmartDeviceApi.getDrList(params);
    }

    // 推送智能设备全部点位信息
    MsgManager.getInstance().sendMsg('smart-device-all-point-mark', {
      type: deviceType[index].code,
      data: readList.value,
    });
  };

  const handleClick = (row, index, tabIndex) => {
    selectedIndex.value = index;
    // 推送智能设备单个点位信息
    MsgManager.getInstance().sendMsg('smart-device-point-mark', {
      type: deviceType[tabIndex].code,
      record: row,
    });
  };

  const handleSearch = () => {
    initReadStatus(props.tabIndex);
  };

  onMounted(() => {
    initReadStatus(props.tabIndex);
  });

  watch(
    () => props.tabIndex,
    (newValue) => {
      baseInfo.value.icon = deviceType[newValue].icon;
      baseInfo.value.label = deviceType[newValue].label;
      baseInfo.value.field = deviceType[newValue].field;
      searchValue.value = '';
      initReadStatus(newValue);
    },
  );
</script>

<style lang="less" scoped>
  .real-status {
    .device-status-info-layout {
      width: 100%;
      margin-bottom: 0.18rem;
      padding: 0.28rem 0.36rem 0;
    }

    .content-layout {
      padding: 0 0.36rem;

      .list-box {
        height: 68vh;
        overflow-y: auto;
        background-color: rgb(6 26 51 / 60%);

        .list-item {
          display: flex;
          align-items: center;
          justify-content: space-between;
          height: 0.35rem;
          padding: 0 0.04rem;
          border-bottom: 1px solid rgb(255 255 255 / 10%);
          font-size: 0.12rem;
          cursor: pointer;

          .list-item-label {
            width: 90%;
            overflow: hidden;
            color: rgb(255 255 255 / 80%);
            text-overflow: ellipsis;
            white-space: nowrap;
          }

          .list-item-status {
            &.online {
              color: #52c41a;
            }

            &.offline {
              color: #ff4d4f;
            }
          }

          &.selected,
          &:hover {
            background-color: rgb(24 144 255 / 8%);

            .list-item-label {
              color: rgb(24 144 255 / 80%);
            }
          }
        }
      }
    }
  }
</style>
